@import url(https://fonts.googleapis.com/css?family=Lato:900);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font: 14px "Lucida Grande", Helvetica, Arial, sans-serif;
  background: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.container, button {
  background-color: #8A24B2;
  color: white;
  border: solid 3px transparent;
}

.hover-effect:hover, *:focus {
  border: solid 1px #4b20e9;
  box-shadow: 1px 1px 1px 1px rgb(141, 141, 141);
}

input {
  border: solid 1px #c1c1c1;
  border-radius: 18px;
  outline: none;
  height: 30px;
  display: flex;
  flex: 100% 1;
  padding-left: 30px;
}

input:invalid {
  border: 1px solid red;
}

a:link {
  color: #4b20e9;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

a:visited {
  color: #8A24B2;
}



#input-search {
    border: solid 1px #c1c1c1;
    border-radius: 18px;
    outline: none;
    height: 30px;
    display: flex;
    flex: 100% 1;
    padding-left: 30px;
}

#input-search:invalid {
    border: 1px solid red;
}

.container-search {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container-search input {
    width: 70vw;
    max-width: 500px;
}

.container-button {
    padding-top: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container-button button {
    width: 150px;
    height: 25px;
    border: none;
    border-radius: 4px;
}

.container-button button:active {
    background: #a4a3a3;
}

.container-cards {
    margin-top: 15px;
    margin-bottom: 30px;
}

.container-card {
    border: solid 1px transparent;
    border-radius: 12px;
    margin-bottom: 15px;
    padding: 8px;
    background-color: #8A24B2;
}

.container-search {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
}

main {
    height: calc(100vh - 80px);
    width: 100%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    /*justify-content: center;*/
    align-items: center;
    z-index: 2;
    margin-top: 50px;
    overflow-y: auto;
}

footer, header {
    width: 100%;
    height: 10vh;
    z-index: 1;
}

