.container-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin: auto;
    width: 100%;
    text-align: center;
    padding: 0 20px;
    position: fixed;
    height: 50px;
    top: 0;
}

.navbar-left, .navbar-right {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.navbar-left {
    left: 0;
}

.navbar-right {
    right: 0;
}

.navbar-left .menu,
.navbar-right .menu {
    list-style: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 0;
    margin-top: 20px;
}

.navbar-left .menu li,
.navbar-right .menu li {
    padding: 0 10px;
}

.container-header h1 {
    display: -webkit-inline-box;
}

.container-header.big h1 {
    font-size: 1.5em;
    margin: 0;
}

.container-header.big img {
    width: 2.5em;
}

.container-header img {
    padding-right: 10px;
    transform: rotate(-8deg);
}

.container-header h1::first-letter {
    color: #8A24B2;
}

.container-footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    padding: 8px;
    left: 0;
    height: 30px;
}

.container-footer p {
    margin: 0;
}

.container-posts form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
    padding: 20px;
}

div.container-images {

}


.root-login {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.container-login {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #e9e9e9;
    padding: 40pt;
    border-radius: 10px;
    max-width: 400px;
    box-shadow: 4px 4px 4px 1px rgb(210, 210, 210);
}

.container-login input {
    justify-content: center;
    align-items: center;
}

.container-login div {
    flex-direction: row;
}

.container-login .error-message {
    color: red;
}

.container-login button {
    margin: 10px;
}

.container-login input {
    margin-top: 5px;
    margin-bottom: 10px;
}

.container-login .register-link {
    padding: 10px;
}

.container-home {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 90vh;
}

h2.title {
    font-size: 2em;
}

.root-register {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
}

.container-register {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #e9e9e9;
    padding: 40pt;
    border-radius: 10px;
    box-shadow: 4px 4px 4px 1px rgb(210, 210, 210);
}

.container-register input {
    margin-top: 5px;
    margin-bottom: 10px;
}

.container-register .error-message {
    color: red;
    display: block;
    margin: 10px;
}
.container-register button {
    margin: 10px;
}

.container-register .login-link {
    padding: 10px;
}

body, html, #root{
    width: 100%;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

div.foo {
    width: 90%;
    margin: 0 auto;
    text-align: center;
}
.letter {
    font-weight: 900;
    font-size: 1.5em;
    position: relative;
    color: #8A24B2;
    transform-style: preserve-3d;
    perspective: 400px;
    z-index: 1;
}
.letter:before,
.letter:after {
    position: absolute;
    content: attr(data-letter);
    transform-origin: top left;
    top: 0;
    left: 0;
}
.letter,
.letter:before,
.letter:after {
    transition: all 0.3s ease-in-out;
}
.letter:before {
    color: white;
    text-shadow: -1px 0px 1px rgba(255, 255, 255, 0.8),
    1px 0px 1px rgba(0, 0, 0, 0.8);
    z-index: 3;
    transform: rotateX(0deg) rotateY(-20deg) rotateZ(0deg);
}
.letter:after {
    color: rgba(0, 0, 0, 0.11);
    z-index: 2;
    transform: scale(1.13, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg)
    skew(0deg, 1deg);
}
.letter:hover:before {
    color: rgba(255, 255, 255, 0.8);
    transform: rotateX(0deg) rotateY(-40deg) rotateZ(0deg);
}
.letter:hover:after {
    transform: scale(1.08, 1) rotateX(0deg) rotateY(40deg) rotateZ(0deg)
    skew(0deg, 22deg);
}


